  display: flex;
  flex-wrap: wrap;
}

.gallery:before, .gallery:after {
  display: table;
  clear: both;
  width: 100%;
  content: '';
}

.gallery > * {
  padding-right: 7.5px;
  padding-left: 7.5px;
}

html body .gallery > * {
  margin-bottom: 0;
  padding-bottom: 1.8rem;
}

html body .gallery:last-child {
